import React from "react";
import Container from "react-bootstrap/Container";
import Row from "react-bootstrap/Row";
import Col from "react-bootstrap/Col";
import ToggleButton from "react-bootstrap/ToggleButton";
import ToggleButtonGroup from "react-bootstrap/ToggleButtonGroup";
import SectionContainer from "./components/SectionContainer";
import { AppProps, AppState } from "./types";
import Sections from "./utilities/Sections";
import "bootstrap/dist/css/bootstrap.min.css";
import "./styles/app.css";

class App extends React.Component<AppProps, AppState> {
    constructor(props: AppProps) {
        super(props);

        this.state = {
            sectionIndex: 0
        }
    }

    handleSelectedSectionChange = (sectionIndex: number) => this.setState({ sectionIndex });

    render() {
        const sections = Sections;
        const sectionIndex = this.state.sectionIndex;

        return (
            <Container>
                <Row>
                    <Col>
                        <h1 className="name-plate-h1">David Kriz</h1>
                    </Col>
                </Row>
                <Row>
                    <Col>
                        <ToggleButtonGroup name="sectionButtonGroup" type="radio" value={sectionIndex} onChange={this.handleSelectedSectionChange}>
                            {sections.map((s, index) => (<ToggleButton key={index} value={index}>{s.name}</ToggleButton>))}
                        </ToggleButtonGroup>
                    </Col>
                </Row>
                <Row>
                    <Col>
                        <SectionContainer section={sections[sectionIndex]} />
                    </Col>
                </Row>
            </Container>
        );
    }
}

export default App;
