import Container from "react-bootstrap/Container";
import Row from "react-bootstrap/Row";
import Col from "react-bootstrap/Col";
import { useMediaQuery } from "react-responsive";
import { SectionContainerProps, PlatformLink } from "../types";
import PlatformLinkIcon from "./PlatformLinkIcon";

const iconPropsFor = (platformLinks: PlatformLink[]) => platformLinks.map(l => ({
    href: l.href,
    imageSrc: l.platform.imageSrc,
    name: l.platform.name
}));

const SectionContainer = (props: SectionContainerProps) => {
    const section = props.section;
    const platformLinkIconProps = iconPropsFor(section.platformLinks);
    const platformLinkIconCount = platformLinkIconProps.length;
    const rows = [];
    const bodyContent = section.body;

    if (bodyContent !== null) {
        const rowIndex = rows.length + 1;
        const bodyRow = <Row className="section-body-row" key={"row-" + rowIndex}><Col>{bodyContent}</Col></Row>

        rows.push(bodyRow);
    }

    let platformLinkCols = [];

    const iconColsPerRowCount = useMediaQuery({ query: "(min-width: 1024px)" })
        ? 4
        : 2;

    for (let i = 0; i < platformLinkIconCount; i++) {
        const platformLink = platformLinkIconProps[i];

        platformLinkCols.push(
            <Col key={"platform-link-col-" + (platformLinkCols.length + 1)}>
                <PlatformLinkIcon
                    href={platformLink.href}
                    imageSrc={platformLink.imageSrc}
                    name={platformLink.name}
                />
            </Col>
        );

        if (platformLinkCols.length === iconColsPerRowCount || i + 1 === platformLinkIconCount) {
            const row = <Row key={"row-" + (rows.length + 1)}>{platformLinkCols}</Row>;

            rows.push(row);

            platformLinkCols = [];
        }
    }

    return <Container className="platform-links-container">{rows}</Container>;
};

export default SectionContainer;
