import { PlatformLinkIconProps } from '../types';

const PlatformLinkIcon = (props: PlatformLinkIconProps) => (
    <a href={props.href} className="platform-link-icon" target="_blank" rel="noopener noreferrer">
        <img className="platform-link-icon-img" src={props.imageSrc} alt={props.name} title={props.name} />
    </a>
);

export default PlatformLinkIcon;
